import createReduxRoutineMiddleware from "@wordpress/redux-routine";
import { configureStore } from "@reduxjs/toolkit";
import { createReduxStore } from "@wordpress/data";

export { register as registerStore } from "@wordpress/data";

export const createStore = (name, options = {}) => {
  let {
    actions,
    reducers,
    controls = {},
    middleware = [],
    preloadedState = {},
    initialState = {},
    devTools = true
  } = options;

  middleware =
    typeof middleware === "function"
      ? (getDefaultMiddleware) =>
          middleware([
            ...getDefaultMiddleware(),
            createControlsMiddleware(controls)
          ])
      : (getDefaultMiddleware) => [
          ...getDefaultMiddleware(),
          createControlsMiddleware(controls),
          ...middleware
        ];

  preloadedState = {
    ...initialState,
    ...preloadedState
  };

  return {
    name,
    instantiate: () => {
      /*       const store = configureStore({
        reducer: reducers,
        middleware,
        devTools,
        preloadedState
      }); */

      const store = createReduxStore(name, {
        reducer: reducers,
        selectors,
        resolvers,
        controls,
        actions
      });

      const subscribe =
        store &&
        ((listener) => {
          let lastState = store.getState().root;

          return store.subscribe(() => {
            const state = store.getState();
            const hasChanged = state !== lastState;
            lastState = state;

            if (hasChanged) {
              listener();
            }
          });
        });
    }
  };
};
